body, html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  background-color: #333;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.search-form {
  width: 80%;
  max-width: 500px;
}

.search-form input {
  width: 70%;
  border: 2px solid #444;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
}

.search-form button {
  width: 20%;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  margin: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ddd;
  border-radius: 5px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  background-color: #f8f8f8;
  justify-content: space-between;
  padding: 10px;
  font-size: .9em;
  display: flex;
}

.info-item {
  margin: 0;
}

.load-more {
  width: auto;
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

@media (max-width: 600px) {
  .search-form input, .search-form button {
    width: 100%;
    margin: 5px 0;
  }

  .search-form button {
    width: auto;
  }

  .gallery {
    grid-template-columns: 1fr;
  }
}

